@import url(https://fonts.googleapis.com/css2?family=Galada&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
#container{
    padding:30px;
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    font-family: 'Roboto', sans-serif;
    
}

#logo{

    font-family: 'Galada', cursive;
    color:black;
    font-size: 40px;
}
#logo-middle{
    font-family: 'Galada', cursive;
    color:#40a9ff;
    font-size: 60px;
    display: flex;
    flex-direction: column;
    align-items: center;
}
#top-nav{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

#middle{
    padding: 50px;;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: center;
}
#middle-btn-grp{
    float: right;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    padding: 20px;
}

#card-grp{
    padding: 30px;
}
 #stats{
     display: flex;
     flex-direction: row;
     flex-wrap: wrap;
     justify-content: space-around;
     align-items: center;
     padding: 20px;
     
 }
#footer{
    padding: 20px;
    text-align: center;
}

